import { CaptionData } from "./types";
import fetch from "node-fetch";

const getCaptionDataforURL = (photoUrl: string) => {
  const KEY = process.env.KEY;
  if (!KEY) return;
  const captionUrl =
    "https://alt-text.cognitiveservices.azure.com/vision/v3.2/describe?maxCandidates=3";
  const captionData = fetch(captionUrl, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "Ocp-Apim-Subscription-Key": KEY,
    },
    body: JSON.stringify({ url: photoUrl }),
  })
    .then((response) => response.json())
    .then((data) => {
      return data as CaptionData;
    });
  return captionData;
};

export const getAllCaptionData = (urlArray: string[]) => {
  return Promise.all(urlArray.map((url) => getCaptionDataforURL(url))).then(
    (data) => {
      return data as CaptionData[];
    }
  );
};
